import React from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'
import './index.css'
import classNames from 'classnames'

function noop () {}

const unmountStacks = []
let __stackIndex = -1

const Barrage = props => {
  const cls = classNames('realsee-design-barrage', props.className)
  let topPositionValue = Math.floor(Math.random() * 100)
  if (topPositionValue < 20 || 80 < topPositionValue ) topPositionValue = 50
  
  return (
      <div className={cls} style={{
        top: `${topPositionValue}%`
      }}>
        <div className="react-barrage-contain">
          {props.children}
        </div>
      </div>
  )
}

Barrage.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node
  ]),
  className: PropTypes.string
}

const __barrageCreater = ({ content, duration = 1, onClose = noop, ...restProps }) => {
  const stackIndex = ++__stackIndex
  const container = document.createElement('div')
  document.body.appendChild(container)
  let unmount = () => {
    unmount = noop
    unmountStacks[stackIndex] = null
    ReactDOM.unmountComponentAtNode(container)
    container.parentNode.removeChild(container)
    onClose()
  }

  unmountStacks[stackIndex] = unmount
  
  ReactDOM.render(<Barrage {...restProps}>{content}</Barrage>, container)

  if (duration !== 0) {
    const aniDuration = window.innerWidth > 768 ? 2 : duration
    setTimeout(unmount, aniDuration * 10000)
  }
  return unmount
}

/**
 * content: string
 * duration: number
 * */

Barrage.message = (...args) => {
  const config = {
    content: args[0],
    className: 'react-barrage'
  }
  if (args[1] !== undefined) {
    config.duration = args[1]
  }
  if (args[2] !== undefined) {
    config.onClose = args[2]
  }
  
  return __barrageCreater(config)
  
}


Barrage.unmount = () => {
  unmountStacks.forEach(unmount => {
    unmount && unmount()
  })
}

export default Barrage
